<template>
    <Select
            :placeholder= placeholder
            filterable
            remote
            :loading="loading"
            @on-open-change="loadRemoteWhenClick">
        <Option v-for="(option, index) in options" :value="option.value" :key="index" @click.native="updateSelectChild(option)">{{option.label}}</Option>
    </Select>
</template>
<script>
    import axios from 'axios';
    export default {
        data () {
            return {
                loading: false,
                options: [{value: 'hhh', label: 'xxx'}],
                list: ['Alabama', 'Alaska', 'Arizona', 'Arkansas', 'California', 'Colorado', 'Connecticut', 'Delaware', 'Florida', 'Georgia', 'Hawaii', 'Idaho', 'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky', 'Louisiana', 'Maine', 'Maryland', 'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi', 'Missouri', 'Montana', 'Nebraska', 'Nevada', 'New hampshire', 'New jersey', 'New mexico', 'New york', 'North carolina', 'North dakota', 'Ohio', 'Oklahoma', 'Oregon', 'Pennsylvania', 'Rhode island', 'South carolina', 'South dakota', 'Tennessee', 'Texas', 'Utah', 'Vermont', 'Virginia', 'Washington', 'West virginia', 'Wisconsin', 'Wyoming']
            }
        },
        // watch: {
        //     selected(curVal){
        //         if (typeof curVal != "undefined") {
        //             this.$emit('updateSelect', curVal);
        //         }
        //     }
        // },
        methods: {
            getLabel: function(sig){
                let methodName = sig['methodName'];
                let returnTypeArray = sig['returnType'].split('.');
                let returnType = returnTypeArray[returnTypeArray.length - 1];
                let params = sig['paramType'].map(item => {
                    let splits = item.split('.');
                    return splits[splits.length - 1];
                });
                if(params.length > 1){
                    params = params.join(', ');
                }
                return returnType + " " + methodName + "(" + params + ")";
            },
            loadRemote: function(ctx, isFilter, query){
                ctx.loading = true;
                setTimeout(() => {
                    ctx.loading = false;

                    axios.get(ctx.reqinfo.url, {
                        params: ctx.reqinfo.params
                    }).then(
                        function (response) {
                            ctx.list = response.data;
                            const list = ctx.list.map(item => {
                                if(ctx.type === 'method'){
                                    return {
                                        value: item,
                                        label: ctx.$options.methods.getLabel(item)
                                    };
                                }
                                return {
                                    value: item,
                                    label: item
                                };
                            });
                            if(isFilter) {
                                ctx.options = list.filter(item => item.label.toLowerCase().indexOf(query.toLowerCase()) > -1);
                            }else {
                                ctx.options = list;
                            }
                        }
                    ).catch(function (error) {
                        console.log(error);
                    })
                }, 200);
            },
            loadRemoteWhenSearch: function(query) {
                if (query !== '') {
                    this.$options.methods.loadRemote(this, true, query);
                } else {
                    this.options = [];
                }
            },
            loadRemoteWhenClick: function(){
                console.log("type is " + this.type);
                this.$options.methods.loadRemote(this, false);
            },
            updateSelectChild: function (option) {
                this.$emit('updateSelect', option.value);
            }
        },
        props: {
            placeholder: String,
            type: String,
            reqinfo: Object
        }
    }
</script>
