<template>
    <Upload
            ref="upload"
            :show-upload-list="true"
            :before-upload="handleBefore"
            :on-error="handleError"
            :format="fmt"
            :on-success="handleSuccess"
            :on-format-error="handleFormatError"
            type="drag"
            :action="url"
            :data="param">
        <div style="padding: 20px 0">
            <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
            <p>{{msg}}</p>
        </div>
    </Upload>
</template>
<script>
    export default {
        props: {
            msg: String,
            fmt: {
                type: Array,
                default: () => []
            },
            url: String
        },
        data: function() {
            return {
                fileList: () => [],
                file: () => null,
                param: {}
            }
        },
        methods: {
            handleBefore: function(file){
                this.file = file;
                this.$emit('updateFile', file);
                this.param = {version: "default"}
                return false;
            },
            upload: function(version){
                this.$set(this.$refs.upload.data, 'version', version);
                this.$refs.upload.post(this.file);
            },
            // eslint-disable-next-line no-unused-vars
            handleSuccess(res, file){
                this.$emit('updateFile', null);
                this.$Notice.success({
                    title: 'Success',
                    render: h => {
                        return h('span', [
                            h('span', {style: {
                                color: 'green'
                            }}, file.name),
                            ' uploaded successfully'
                        ])
                    }
                })
            },
            handleError(){
                this.$Notice.error({
                    title: 'Upload failed'
                })
            },
            handleFormatError(){
                this.$Notice.warning({
                    title: 'The file format is incorrect',
                    desc: 'Please select a file ending with ' + this.fmt.join(' or ')
                });
            }
        }
    }
</script>
