<style lang="less">
    .demo-Circle-custom{
        & h1{
            color: #3f414d;
            font-size: 28px;
            font-weight: normal;
        }
        & p{
            color: #657180;
            font-size: 14px;
            margin: 10px 0 15px;
        }
        & span{
            display: block;
            padding-top: 15px;
            color: #657180;
            font-size: 14px;
            &:before{
                content: '';
                display: block;
                width: 50px;
                height: 1px;
                margin: 0 auto;
                background: #e0e3e6;
                position: relative;
                top: -15px;
            };
        }
        & span i{
            font-style: normal;
            color: #3f414d;
        }
    }
</style>
<template>
    <i-circle
            :size="250"
            :trail-width="4"
            :stroke-width="5"
            :percent=percentage
            stroke-linecap="square"
            stroke-color="green">
        <div class="demo-Circle-custom">
            <h1>{{passed}}</h1>
            <p>测试用例通过数</p>
            <span>
                占总测试用例
                <i>{{percent}}</i>
            </span>
        </div>
    </i-circle>
</template>
<script>
    export default {
        props:{
            percentage: Number,
            passed: Number
        },
        computed: {
            percent(){
                return this.percentage + "%";
            }
        }
    }
</script>
